import React, { useState } from 'react';
import { useQuery, useMutation, useQueryClient } from 'react-query';
import { Link } from 'react-router-dom';
import { 
  Plus, 
  FileText, 
  Image, 
  Video, 
  BookOpen, 
  Award, 
  Edit, 
  Trash2, 
  Download,
  Eye,
  Calendar,
} from 'lucide-react';
import { contentAPI } from '../services/api';
import useAuthStore from '../store/authStore';
import toast from 'react-hot-toast';

const StudentDashboard = () => {
  const { user } = useAuthStore();
  const queryClient = useQueryClient();
  const [selectedCategory, setSelectedCategory] = useState('all');

  // Fetch student's content
  const { data: contentData, isLoading, error } = useQuery(
    ['student-content', user?.id],
    () => contentAPI.getMyContent(),
    {
      enabled: !!user?.id,
      onSuccess: (data) => {
        console.log('Student content API response:', data);
        console.log('Content data structure:', data?.data);
      },
      onError: (error) => {
        console.error('Error fetching student content:', error);
      }
    }
  );

  // Delete content mutation
  const deleteMutation = useMutation(
    (contentId) => contentAPI.delete(contentId),
    {
      onSuccess: () => {
        queryClient.invalidateQueries(['student-content']);
        queryClient.invalidateQueries(['recent-activities']);
        toast.success('Xa ni dung thnh cng!');
      },
      onError: (error) => {
        toast.error(error.response?.data?.message || 'C li xy ra khi xa ni dung');
      }
    }
  );

  const handleDelete = (contentId) => {
    if (window.confirm('Bn c chc chn mun xa ni dung ny?')) {
      deleteMutation.mutate(contentId);
    }
  };

  const getSubCategoryIcon = (subCategory) => {
    switch (subCategory) {
      case 'tai-lieu-hoc-tap':
        return <FileText className="h-5 w-5" />;
      case 'hinh-anh-hoc-tap':
        return <Image className="h-5 w-5" />;
      case 'video-hoc-tap':
        return <Video className="h-5 w-5" />;
      case 'bai-tap-hoc-sinh':
        return <BookOpen className="h-5 w-5" />;
      case 'du-an-hoc-tap':
        return <Award className="h-5 w-5" />;
      case 'san-pham-hoc-tap':
        return <Award className="h-5 w-5" />;
      default:
        return <FileText className="h-5 w-5" />;
    }
  };

  const getSubCategoryLabel = (subCategory) => {
    const labels = {
      'tai-lieu-hoc-tap': 'Ti liu hc tp',
      'hinh-anh-hoc-tap': 'Hnh nh hc tp',
      'video-hoc-tap': 'Video hc tp',
      'bai-tap-hoc-sinh': 'Bi tp hc sinh',
      'du-an-hoc-tap': 'D n hc tp',
      'san-pham-hoc-tap': 'Sn phm hc tp'
    };
    return labels[subCategory] || subCategory;
  };

  const getCategoryLabel = (category) => {
    const labels = {
      'lich-su-10': 'Lch s 10',
      'lich-su-11': 'Lch s 11',
      'lich-su-12': 'Lch s 12',
      'lich-su-dia-phuong': 'Lch s a phng'
    };
    return labels[category] || category;
  };

  // Ensure contentData.data is an array
  const contents = Array.isArray(contentData?.data.data) ? contentData.data.data : [];
  console.log('contents:', contents);
  const filteredContent = contents.filter(content => {
    if (selectedCategory === 'all') return true;
    return content.subCategory === selectedCategory;
  });

  const categories = [
    { value: 'all', label: 'Tt c', count: contents.length },
    { value: 'tai-lieu-hoc-tap', label: 'Ti liu hc tp', count: contents.filter(c => c.subCategory === 'tai-lieu-hoc-tap').length },
    { value: 'hinh-anh-hoc-tap', label: 'Hnh nh hc tp', count: contents.filter(c => c.subCategory === 'hinh-anh-hoc-tap').length },
    { value: 'video-hoc-tap', label: 'Video hc tp', count: contents.filter(c => c.subCategory === 'video-hoc-tap').length },
    { value: 'bai-tap-hoc-sinh', label: 'Bi tp hc sinh', count: contents.filter(c => c.subCategory === 'bai-tap-hoc-sinh').length },
    { value: 'du-an-hoc-tap', label: 'D n hc tp', count: contents.filter(c => c.subCategory === 'du-an-hoc-tap').length },
    { value: 'san-pham-hoc-tap', label: 'Sn phm hc tp', count: contents.filter(c => c.subCategory === 'san-pham-hoc-tap').length }
  ];

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gray-50 py-8">
        <div className="container mx-auto px-4">
          <div className="flex justify-center items-center h-64">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="min-h-screen bg-gray-50 py-8">
        <div className="container mx-auto px-4">
          <div className="text-center">
            <div className="text-8xl mb-6"></div>
            <h2 className="text-2xl font-bold text-red-600 mb-4">Li ti d liu</h2>
            <p className="text-gray-600 mb-8">{error.message || 'C li xy ra khi ti d liu'}</p>
            <button
              onClick={() => window.location.reload()}
              className="bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 transition-colors"
            >
              Th li
            </button>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="container mx-auto px-4">
        {/* Header */}
        <div className="bg-white rounded-lg shadow-sm p-6 mb-8">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between">
            <div>
              <h1 className="text-3xl font-bold text-gray-900 mb-2">
                Dashboard Hc Sinh
              </h1>
              <p className="text-gray-600">
                Qun l ti liu hc tp, sn phm v hnh nh hc tp ca bn
              </p>
            </div>
            <Link
              to="/upload"
              className="mt-4 md:mt-0 inline-flex items-center px-6 py-3 bg-blue-600 text-white font-medium rounded-lg hover:bg-blue-700 transition-colors"
            >
              <Plus className="h-5 w-5 mr-2" />
              ng ti ni dung mi
            </Link>
          </div>
        </div>

        {/* Stats */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <div className="bg-white rounded-lg shadow-sm p-6">
            <div className="flex items-center">
              <div className="p-3 bg-blue-100 rounded-lg">
                <FileText className="h-6 w-6 text-blue-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Tng s ni dung</p>
                <p className="text-2xl font-bold text-gray-900">{contents.length}</p>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-lg shadow-sm p-6">
            <div className="flex items-center">
              <div className="p-3 bg-green-100 rounded-lg">
                <Eye className="h-6 w-6 text-green-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Lt xem</p>
                <p className="text-2xl font-bold text-gray-900">
                  {contents.reduce((sum, content) => sum + (content.viewCount || 0), 0)}
                </p>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-lg shadow-sm p-6">
            <div className="flex items-center">
              <div className="p-3 bg-purple-100 rounded-lg">
                <Download className="h-6 w-6 text-purple-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Lt ti</p>
                <p className="text-2xl font-bold text-gray-900">
                  {contents.reduce((sum, content) => sum + (content.downloadCount || 0), 0)}
                </p>
              </div>
            </div>
          </div>

          <div className="bg-white rounded-lg shadow-sm p-6">
            <div className="flex items-center">
              <div className="p-3 bg-yellow-100 rounded-lg">
                <Award className="h-6 w-6 text-yellow-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600"> ph duyt</p>
                <p className="text-2xl font-bold text-gray-900">
                  {contents.filter(content => content.isApproved).length}
                </p>
              </div>
            </div>
          </div>
        </div>

        {/* Pending Approval Notice */}
        {contents.filter(content => !content.isApproved).length > 0 && (
          <div className="bg-yellow-50 border border-yellow-200 rounded-lg p-4 mb-8">
            <div className="flex items-center">
              <div className="flex-shrink-0">
                <svg className="h-5 w-5 text-yellow-400" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                </svg>
              </div>
              <div className="ml-3">
                <h3 className="text-sm font-medium text-yellow-800">
                  C {contents.filter(content => !content.isApproved).length} ni dung ang ch ph duyt
                </h3>
                <p className="text-sm text-yellow-700 mt-1">
                  Ni dung s c hin th cng khai sau khi gio vin ph duyt
                </p>
              </div>
            </div>
          </div>
        )}

        {/* Category Filter */}
        <div className="bg-white rounded-lg shadow-sm p-6 mb-8">
          <h2 className="text-xl font-semibold text-gray-900 mb-4">Lc theo loi ni dung</h2>
          <div className="flex flex-wrap gap-3">
            {categories.map((category) => (
              <button
                key={category.value}
                onClick={() => setSelectedCategory(category.value)}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
                  selectedCategory === category.value
                    ? 'bg-blue-600 text-white'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                {category.label} ({category.count})
              </button>
            ))}
          </div>
        </div>

        {/* Content List */}
        <div className="bg-white rounded-lg shadow-sm">
          <div className="p-6 border-b border-gray-200">
            <h2 className="text-xl font-semibold text-gray-900">
              Ni dung ca bn
            </h2>
          </div>

          {filteredContent.length === 0 ? (
            <div className="p-12 text-center">
              <FileText className="mx-auto h-12 w-12 text-gray-400 mb-4" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">
                {contents.length === 0 ? 'Cha c ni dung no' : 'Khng c ni dung trong danh mc ny'}
              </h3>
              <p className="text-gray-600 mb-6">
                {contents.length === 0 
                  ? 'Bt u ng ti ti liu hc tp, sn phm hoc hnh nh hc tp ca bn'
                  : 'Th chn danh mc khc hoc ng ti ni dung mi'
                }
              </p>
              <div className="flex flex-col sm:flex-row gap-4 justify-center">
                <Link
                  to="/upload"
                  className="inline-flex items-center px-6 py-3 bg-blue-600 text-white font-medium rounded-lg hover:bg-blue-700 transition-colors"
                >
                  <Plus className="h-5 w-5 mr-2" />
                  ng ti ni dung mi
                </Link>
                {contents.length > 0 && (
                  <button
                    onClick={() => setSelectedCategory('all')}
                    className="inline-flex items-center px-6 py-3 border border-gray-300 text-gray-700 font-medium rounded-lg hover:bg-gray-50 transition-colors"
                  >
                    Xem tt c ni dung
                  </button>
                )}
              </div>
            </div>
          ) : (
            <div className="divide-y divide-gray-200">
              {filteredContent.map((content) => (
                <div key={content._id} className="p-6 hover:bg-gray-50 transition-colors">
                  <div className="flex items-start justify-between">
                    <div className="flex items-start space-x-4 flex-1">
                      <div className="p-2 bg-blue-100 rounded-lg">
                        {getSubCategoryIcon(content.subCategory)}
                      </div>
                      
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center space-x-2 mb-2">
                          <h3 className="text-lg font-medium text-gray-900 truncate">
                            {content.title}
                          </h3>
                          {content.isApproved ? (
                            <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                               ph duyt
                            </span>
                          ) : (
                            <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
                              Ch ph duyt
                            </span>
                          )}
                        </div>
                        
                        {content.description ? (
                          <div 
                            className="text-sm text-gray-600 mb-2"
                            dangerouslySetInnerHTML={{ __html: content.description }}
                          />
                        ) : (
                          <p className="text-sm text-gray-600 mb-2">Khng c m t</p>
                        )}
                        
                        <div className="flex flex-wrap items-center gap-4 text-sm text-gray-500">
                          <span className="flex items-center" title="Loi ni dung">
                            <FileText className="h-4 w-4 mr-1" />
                            {getSubCategoryLabel(content.subCategory)}
                          </span>
                          <span className="flex items-center" title="Danh mc">
                            <BookOpen className="h-4 w-4 mr-1" />
                            {getCategoryLabel(content.category)}
                          </span>
                          <span className="flex items-center" title="Lt xem">
                            <Eye className="h-4 w-4 mr-1" />
                            {content.viewCount || 0} lt xem
                          </span>
                          <span className="flex items-center" title="Lt ti">
                            <Download className="h-4 w-4 mr-1" />
                            {content.downloadCount || 0} lt ti
                          </span>
                          <span className="flex items-center" title="Ngy to">
                            <Calendar className="h-4 w-4 mr-1" />
                            {new Date(content.createdAt).toLocaleDateString('vi-VN')}
                          </span>
                        </div>
                      </div>
                    </div>
                    
                    <div className="flex items-center space-x-2 ml-4">
                      <Link
                        to={`/content/${content._id}`}
                        className="p-2 text-gray-400 hover:text-gray-600 transition-colors"
                        title="Xem chi tit"
                      >
                        <Eye className="h-5 w-5" />
                      </Link>
                      
                      <Link
                        to={`/upload?edit=${content._id}`}
                        className="p-2 text-blue-400 hover:text-blue-600 transition-colors"
                        title="Chnh sa"
                      >
                        <Edit className="h-5 w-5" />
                      </Link>
                      
                      <button
                        onClick={() => handleDelete(content._id)}
                        className="p-2 text-red-400 hover:text-red-600 transition-colors"
                        title="Xa"
                        disabled={deleteMutation.isLoading}
                      >
                        <Trash2 className="h-5 w-5" />
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default StudentDashboard; 