import React, { useState } from 'react';
import { useQuery, useMutation } from 'react-query';
import { 
  Users, 
  FileText, 
  Plus, 
  BarChart3, 
  Eye,
  CheckCircle,
  XCircle,
  UserPlus
} from 'lucide-react';
import { authAPI, contentAPI, userAPI } from '../services/api';
import useAuthStore from '../store/authStore';
import toast from 'react-hot-toast';

const TeacherDashboard = () => {
  const { user } = useAuthStore();
  const [activeTab, setActiveTab] = useState('overview');
  const [showCreateTeacher, setShowCreateTeacher] = useState(false);
  const [createTeacherData, setCreateTeacherData] = useState({
    fullName: '',
    phone: '',
    email: '',
    school: '',
    password: '',
    confirmPassword: ''
  });

  // Fetch dashboard data
  const { data: dashboardData, isLoading } = useQuery(
    ['teacher-dashboard'],
    async () => {
      const [contentStats, userStats] = await Promise.all([
        contentAPI.getMyContent({ limit: 100 }), // Ly nhiu hn  hin th y 
        userAPI.getAll({ role: 'student' })
      ]);
      
      console.log('Dashboard data:', {
        contentStats: contentStats.data,
        userStats: userStats.data,
        user: user
      });
      
      return {
        myContent: contentStats.data.data,
        students: userStats.data.data,
        totalStudents: userStats.data.data?.length || 0,
        totalContent: contentStats.data.data?.length || 0,
        approvedContent: contentStats.data.data?.filter(c => c.isApproved).length || 0,
        pendingContent: contentStats.data.data?.filter(c => !c.isApproved).length || 0
      };
    },
    {
      onError: (error) => {
        console.error('Dashboard data error:', error);
      }
    }
  );

  // Create teacher mutation
  const createTeacherMutation = useMutation(
    authAPI.createTeacher,
    {
      onSuccess: () => {
        toast.success('To ti khon gio vin thnh cng!');
        setShowCreateTeacher(false);
        setCreateTeacherData({
          fullName: '',
          phone: '',
          email: '',
          school: '',
          password: '',
          confirmPassword: ''
        });
      },
      onError: (error) => {
        const message = error.response?.data?.message || 'C li xy ra';
        toast.error(message);
      }
    }
  );

  const handleCreateTeacher = (e) => {
    e.preventDefault();
    
    if (createTeacherData.password !== createTeacherData.confirmPassword) {
      toast.error('Mt khu xc nhn khng khp');
      return;
    }

    if (createTeacherData.password.length < 6) {
      toast.error('Mt khu phi c t nht 6 k t');
      return;
    }

    const { confirmPassword, ...submitData } = createTeacherData;
    createTeacherMutation.mutate(submitData);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setCreateTeacherData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const stats = [
    {
      title: 'Tng ti liu',
      value: dashboardData?.totalContent || 0,
      icon: FileText,
      color: 'bg-amber-500',
      change: '+12%',
      changeType: 'increase'
    },
    {
      title: 'Ti liu  ph duyt',
      value: dashboardData?.approvedContent || 0,
      icon: CheckCircle,
      color: 'bg-green-500',
      change: '+8%',
      changeType: 'increase'
    },
    {
      title: 'Ti liu ch ph duyt',
      value: dashboardData?.pendingContent || 0,
      icon: Eye,
      color: 'bg-yellow-500',
      change: '-3%',
      changeType: 'decrease'
    },
    {
      title: 'Hc sinh ng k',
      value: dashboardData?.totalStudents || 0,
      icon: Users,
      color: 'bg-blue-500',
      change: '+15%',
      changeType: 'increase'
    }
  ];

  const recentContent = dashboardData?.myContent?.slice(0, 5) || [];
  const allMyContent = dashboardData?.myContent || [];
  console.log('dashboardData:', dashboardData); 
  console.log('recentContent:', recentContent);
  console.log('allMyContent:', allMyContent);
  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="container mx-auto px-4">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            Dashboard Gio vin
          </h1>
          <p className="text-gray-600">
            Cho mng tr li, {user?.fullName}! y l tng quan v hot ng ca bn.
          </p>
        </div>

        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
          {stats.map((stat, index) => (
            <div key={index} className="bg-white rounded-lg shadow-sm p-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-sm font-medium text-gray-600">{stat.title}</p>
                  <p className="text-2xl font-bold text-gray-900">{stat.value}</p>
                </div>
                <div className={`${stat.color} p-3 rounded-lg`}>
                  <stat.icon className="h-6 w-6 text-white" />
                </div>
              </div>
              <div className="mt-4 flex items-center">
                <span className={`text-sm font-medium ${
                  stat.changeType === 'increase' ? 'text-green-600' : 'text-red-600'
                }`}>
                  {stat.change}
                </span>
                <span className="text-sm text-gray-500 ml-2">so vi thng trc</span>
              </div>
            </div>
          ))}
        </div>

        {/* Tabs */}
        <div className="bg-white rounded-lg shadow-sm mb-8">
          <div className="border-b border-gray-200">
            <nav className="flex space-x-8 px-6">
              <button
                onClick={() => setActiveTab('overview')}
                className={`py-4 px-1 border-b-2 font-medium text-sm ${
                  activeTab === 'overview'
                    ? 'border-amber-500 text-amber-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                <BarChart3 className="h-5 w-5 inline mr-2" />
                Tng quan
              </button>
              <button
                onClick={() => setActiveTab('content')}
                className={`py-4 px-1 border-b-2 font-medium text-sm ${
                  activeTab === 'content'
                    ? 'border-amber-500 text-amber-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                <FileText className="h-5 w-5 inline mr-2" />
                Ti liu ca ti
              </button>
              <button
                onClick={() => setActiveTab('teachers')}
                className={`py-4 px-1 border-b-2 font-medium text-sm ${
                  activeTab === 'teachers'
                    ? 'border-amber-500 text-amber-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                }`}
              >
                <Users className="h-5 w-5 inline mr-2" />
                Qun l gio vin
              </button>
            </nav>
          </div>

          <div className="p-6">
            {activeTab === 'overview' && (
              <div>
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                  {/* Recent Activity */}
                  <div>
                    <h3 className="text-lg font-semibold text-gray-900 mb-4">
                      Hot ng gn y
                    </h3>
                    <div className="space-y-4">
                      {recentContent.map((content) => (
                        <div key={content._id} className="flex items-center space-x-3 p-3 bg-gray-50 rounded-lg">
                          <div className="flex-shrink-0">
                            <FileText className="h-5 w-5 text-amber-600" />
                          </div>
                          <div className="flex-1 min-w-0">
                            <p className="text-sm font-medium text-gray-900 truncate">
                              {content.title}
                            </p>
                            <p className="text-sm text-gray-500">
                              {new Date(content.createdAt).toLocaleDateString('vi-VN')}
                            </p>
                          </div>
                          <div className="flex-shrink-0">
                            <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                              content.isApproved 
                                ? 'bg-green-100 text-green-800' 
                                : 'bg-yellow-100 text-yellow-800'
                            }`}>
                              {content.isApproved ? ' ph duyt' : 'Ch ph duyt'}
                            </span>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>

                  {/* Quick Actions */}
                  <div>
                    <h3 className="text-lg font-semibold text-gray-900 mb-4">
                      Thao tc nhanh
                    </h3>
                    <div className="space-y-3">
                      <button
                        onClick={() => window.location.href = '/upload'}
                        className="w-full flex items-center justify-center px-4 py-3 border border-amber-300 text-amber-700 bg-amber-50 rounded-lg hover:bg-amber-100 transition-colors"
                      >
                        <Plus className="h-5 w-5 mr-2" />
                        ng ti ti liu mi
                      </button>
                      <button
                        onClick={() => setShowCreateTeacher(true)}
                        className="w-full flex items-center justify-center px-4 py-3 border border-amber-300 text-amber-700 bg-amber-50 rounded-lg hover:bg-amber-100 transition-colors"
                      >
                        <UserPlus className="h-5 w-5 mr-2" />
                        To ti khon gio vin
                      </button>
                      <button
                        onClick={() => window.location.href = '/admin'}
                        className="w-full flex items-center justify-center px-4 py-3 border border-amber-300 text-amber-700 bg-amber-50 rounded-lg hover:bg-amber-100 transition-colors"
                      >
                        <Users className="h-5 w-5 mr-2" />
                        Qun l h thng
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            )}

            {activeTab === 'content' && (
              <div>
                <div className="flex justify-between items-center mb-6">
                  <h3 className="text-lg font-semibold text-gray-900">
                    Ti liu ca ti
                  </h3>
                  <button
                    onClick={() => window.location.href = '/upload'}
                    className="flex items-center px-4 py-2 bg-amber-700 text-white rounded-lg hover:bg-amber-600 transition-colors"
                  >
                    <Plus className="h-4 w-4 mr-2" />
                    ng ti mi
                  </button>
                </div>

                {isLoading ? (
                  <div className="flex justify-center items-center h-64">
                    <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-amber-600"></div>
                  </div>
                ) : (
                  <div className="overflow-x-auto">
                    <table className="min-w-full divide-y divide-gray-200">
                      <thead className="bg-gray-50">
                        <tr>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Ti liu
                          </th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Danh mc
                          </th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Trng thi
                          </th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Lt xem
                          </th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                            Ngy to
                          </th>
                        </tr>
                      </thead>
                      <tbody className="bg-white divide-y divide-gray-200">
                        {Array.isArray(allMyContent) && allMyContent.length > 0 ? (
                          allMyContent.map((content) => (
                            <tr key={content._id}>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <div className="text-sm font-medium text-gray-900">
                                  {content.title}
                                </div>
                                <div className="text-sm text-gray-500">
                                  {content.fileName}
                                </div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <span className="text-sm text-gray-900">
                                  {content.category} / {content.subCategory}
                                </span>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                                  content.isApproved 
                                    ? 'bg-green-100 text-green-800' 
                                    : 'bg-yellow-100 text-yellow-800'
                                }`}>
                                  {content.isApproved ? ' ph duyt' : 'Ch ph duyt'}
                                </span>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                                {content.viewCount}
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                {new Date(content.createdAt).toLocaleDateString('vi-VN')}
                              </td>
                            </tr>
                          ))
                        ) : (
                          <tr>
                            <td colSpan="5" className="px-6 py-12 text-center">
                              <div className="text-center">
                                <FileText className="mx-auto h-12 w-12 text-gray-400 mb-4" />
                                <h3 className="text-lg font-medium text-gray-900 mb-2">
                                  Cha c ti liu no
                                </h3>
                                <p className="text-gray-500 mb-4">
                                  Bn cha ng ti ti liu no. Hy bt u bng cch to ti liu mi!
                                </p>
                                <button
                                  onClick={() => window.location.href = '/upload'}
                                  className="inline-flex items-center px-4 py-2 bg-amber-700 text-white rounded-lg hover:bg-amber-600 transition-colors"
                                >
                                  <Plus className="h-4 w-4 mr-2" />
                                  ng ti ti liu u tin
                                </button>
                              </div>
                            </td>
                          </tr>
                        )}
                      </tbody>
                    </table>
                  </div>
                )}
              </div>
            )}

            {activeTab === 'teachers' && (
              <div>
                <div className="flex justify-between items-center mb-6">
                  <h3 className="text-lg font-semibold text-gray-900">
                    Qun l gio vin
                  </h3>
                  <button
                    onClick={() => setShowCreateTeacher(true)}
                    className="flex items-center px-4 py-2 bg-amber-700 text-white rounded-lg hover:bg-amber-600 transition-colors"
                  >
                    <UserPlus className="h-4 w-4 mr-2" />
                    To ti khon gio vin
                  </button>
                </div>

                <div className="bg-amber-50 border border-amber-200 rounded-lg p-6">
                  <div className="flex items-center mb-4">
                    <UserPlus className="h-6 w-6 text-amber-600 mr-2" />
                    <h4 className="text-lg font-semibold text-amber-900">
                      To ti khon gio vin mi
                    </h4>
                  </div>
                  <p className="text-amber-800 mb-4">
                    Ch gio vin mi c quyn to ti khon gio vin khc. 
                    Ti khon mi s c kch hot ngay lp tc v c th ng nhp.
                  </p>
                  <button
                    onClick={() => setShowCreateTeacher(true)}
                    className="bg-amber-700 text-white px-4 py-2 rounded-lg hover:bg-amber-600 transition-colors"
                  >
                    Bt u to
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Create Teacher Modal */}
        {showCreateTeacher && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-white rounded-lg p-8 max-w-md w-full mx-4">
              <div className="flex justify-between items-center mb-6">
                <h3 className="text-lg font-semibold text-gray-900">
                  To ti khon gio vin
                </h3>
                <button
                  onClick={() => setShowCreateTeacher(false)}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <XCircle className="h-6 w-6" />
                </button>
              </div>

              <form onSubmit={handleCreateTeacher} className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    H v tn *
                  </label>
                  <input
                    type="text"
                    name="fullName"
                    value={createTeacherData.fullName}
                    onChange={handleInputChange}
                    required
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-500 focus:border-transparent"
                    placeholder="Nhp h v tn"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    S in thoi *
                  </label>
                  <input
                    type="tel"
                    name="phone"
                    value={createTeacherData.phone}
                    onChange={handleInputChange}
                    required
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-500 focus:border-transparent"
                    placeholder="Nhp s in thoi"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Email
                  </label>
                  <input
                    type="email"
                    name="email"
                    value={createTeacherData.email}
                    onChange={handleInputChange}
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-500 focus:border-transparent"
                    placeholder="Nhp email (khng bt buc)"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Trng hc *
                  </label>
                  <input
                    type="text"
                    name="school"
                    value={createTeacherData.school}
                    onChange={handleInputChange}
                    required
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-500 focus:border-transparent"
                    placeholder="Nhp tn trng hc"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Mt khu *
                  </label>
                  <input
                    type="password"
                    name="password"
                    value={createTeacherData.password}
                    onChange={handleInputChange}
                    required
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-500 focus:border-transparent"
                    placeholder="Nhp mt khu (t nht 6 k t)"
                  />
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Xc nhn mt khu *
                  </label>
                  <input
                    type="password"
                    name="confirmPassword"
                    value={createTeacherData.confirmPassword}
                    onChange={handleInputChange}
                    required
                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-500 focus:border-transparent"
                    placeholder="Nhp li mt khu"
                  />
                </div>

                <div className="flex justify-end space-x-3 pt-4">
                  <button
                    type="button"
                    onClick={() => setShowCreateTeacher(false)}
                    className="px-4 py-2 border border-gray-300 rounded-lg text-gray-700 hover:bg-gray-50"
                  >
                    Hy
                  </button>
                  <button
                    type="submit"
                    disabled={createTeacherMutation.isLoading}
                    className="px-4 py-2 bg-amber-700 text-white rounded-lg hover:bg-amber-600 disabled:opacity-50"
                  >
                    {createTeacherMutation.isLoading ? 'ang to...' : 'To ti khon'}
                  </button>
                </div>
              </form>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default TeacherDashboard; 